<template>
  <div class="container">
    <div class="modal-widget" v-if="!signup">
      <div class="header">
        <div class="btn-close" @click="CLOSE_SELECT"></div>
      </div>
      <div class="content">
        <h2 class="title-modal">Choose your service</h2>
        <input
          class="search-input"
          v-model="serviceSearch"
          placeholder="Search"
        />
        <div class="recent block">
          <div class="title">Recent</div>
          <div class="buttons-container">
            <div
              v-for="(recent, index) in recents"
              v-bind:key="index"
              v-bind:is="recent"
            ></div>
          </div>
        </div>
        <div class="all block">
          <div class="title">All</div>
          <div class="buttons-container">
            <div
              v-for="(button, index) in buttons"
              v-bind:key="index"
              v-bind:is="button"
              class="button"
            ></div>
          </div>
        </div>
      </div>
    </div>
    <GoogleSignup v-if="signup" />
  </div>
</template>

<script>
import GoogleAnalytics from "@/components/servicesSelect/googleAnalyticsBtn";
import GoogleSignup from "@/components/servicesSelect/googleSignup";
import GoogleSearchConsole from "@/components/servicesSelect/searchConsoleBtn";

import { mapMutations, mapState } from "vuex";
export default {
  computed: {
    ...mapState("servicesSelect", ["recents", "signup", "signupWindow"])
  },
  data() {
    return {
      components: ["GoogleAnalytics", "GoogleSearchConsole"],
      buttons: ["GoogleAnalytics", "GoogleSearchConsole"],
      serviceSearch: ""
    };
  },
  methods: {
    ...mapMutations(["CLOSE_SELECT"]),
    ...mapMutations("servicesSelect", ["OPEN_SIGNUP", "CLOSE_SIGNUP"])
  },
  watch: {
    serviceSearch: function(val) {
      this.buttons = this.components.filter(e => {
        if (e.toUpperCase().search(val.split(' ').join('').toUpperCase()) >= 0) {
          return true;
        }
        return false;
      });
    }
  },
  components: {
    GoogleAnalytics,
    GoogleSearchConsole,
    GoogleSignup
  }
};
</script>

<style scoped>
.container {
  /* position: absolute; */
  /* z-index: 999; */
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  /* left: 0; */
  /* top: 0; */
  /* background-color: rgba(0, 0, 0, 0.6); */
}
.modal-widget {
  width: 150rem;
  height: 85rem;
  border-radius: 2rem;
  background-color: #1c1c1c;
  user-select: none;
}

.search-input {
  background-image: url("../assets/images/search/Vector.svg");
  background-repeat: no-repeat;
  background-size: 1.5vw;
  background-position: 0.5vw center;
  padding: 0 2.5vw;
  font-size: 1.5vw;
}

.buttons-container{
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}

.block,
.search-input {
  margin-bottom: 2.5vw;
}

.header {
  width: 100%;
  height: 3vw;
  display: flex;
  align-items: flex-end;
  padding: 0.75vw;
}

.btn-close {
  width: 1.5vw;
  height: 1.5vw;
  background: url("../assets/images/modal_widget_settings/btn_close.svg")
    no-repeat center center / contain;
  margin-left: auto;
  cursor: pointer;
}
.btn-close:hover {
  filter: drop-shadow(0px 0px 5px rgb(253, 2, 2));
}

.search-input {
  background-color: #262626;
  border: none;
  color: #ffffff;
}

.content {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: calc(100% - 60px);
  padding: 0 2vw 2vw;
}

.title {
  color: white;
  font-size: 2vw;
  width: 100%;
  border-bottom: 3px solid #7873ff;
  text-align: start;
}

.title-modal {
  color: white;
  font-weight: normal;
  font-size: 2.5vw;
  width: 100%;
  text-align: start;
  margin-bottom: 1.5vw;
}
</style>
