<template>
  <div class="top-panel">
    <div class="add-btn">
      <img
        src="../assets/images/top_panel_btn/plus.svg"
        alt="plus"
        @click="OPEN_MODAL"
      />
    </div>
    <div class="top-btn">
      <img
        src="../assets/images/top_panel_btn/copy.svg"
        alt="copy"
        @click="copyWidget"
      />
    </div>
    <div class="top-btn">
      <img
        src="../assets/images/top_panel_btn/delete.svg"
        alt="delete"
        @click="deleteWidget"
      />
    </div>
    <!-- <div class="top-btn">
      <img src="../assets/images/top_panel_btn/share.svg" alt="share" />
    </div>
    <div class="top-btn">
      <img src="../assets/images/top_panel_btn/download.svg" alt="download" />
    </div> -->
  </div>
</template>

<script>
import { mapMutations, mapGetters } from "vuex";
export default {
  methods: {
    ...mapMutations(["OPEN_MODAL"]),
    ...mapMutations("dashboards", ["DELETE_WIDGET", "ADD_NEW_WIDGET"]),

    deleteWidget() {
      const w = this.getSelectedWidget;
      w ? this.DELETE_WIDGET(w.name) : false;
    },

    copyWidget() {
      const w = this.getSelectedWidget;
      if (w) {
        switch (true) {
          case w.name.includes("signups"):
            w.anSite = "ga";
            break;

          case w.name.includes("revenu"):
            w.anSite = "revenu";
            break;

          default:
            break;
        }
        this.ADD_NEW_WIDGET(w);
      }
    }
  },

  computed: {
    ...mapGetters("dashboards", ["getSelectedWidget"])
  }
};
</script>

<style lang="scss" scoped>
.top-panel {
  display: flex;
}

.top-btn,
.add-btn {
  width: 5.2rem;
  height: 5.2rem;
  background-color: #262626;
  opacity: 0.7;
  display: flex;
  align-items: center;
  justify-content: center;
  user-select: none;
  margin-right: 3.5rem;
}

.top-btn {
  border-radius: 1.5rem;

  img {
    width: 60%;
    height: 60%;
  }
}

.add-btn {
  border-radius: 50%;

  img {
    width: 100%;
    height: 100%;
  }
}

.top-btn:active,
.add-btn:active {
  background-color: #202020;
}

.top-btn:hover,
.add-btn:hover {
  box-shadow: 0.4rem 0.4rem 0.7rem 0.1rem rgba(0, 0, 0, 0.3),
    -0.3rem -0.3rem 0.7rem 0.1rem rgba(255, 255, 255, 0.2);
  opacity: 1;
}
</style>
