export default {
  sendSettings: (token, settings) => {
    const body = {
      token: token,
      settings: settings
    };
    fetch("https://melytix.herokuapp.com/put-dash-settings", {
      method: "POST",
      headers: {
        "Content-Type": "application/json;charset=utf-8"
      },
      body: JSON.stringify(body)
    });
    // .then(res => res.json())
    // .then(d => console.log(d));
  },
  getSettings: async token => {
    const body = { token: token };
    const result = await fetch(
      "https://melytix.herokuapp.com/get-dash-settings",
      {
        method: "POST",
        headers: {
          "Content-Type": "application/json;charset=utf-8"
        },
        body: JSON.stringify(body)
      }
    )
      .then(res => res.json())
      .then(data => data)
      .catch(err => console.log(err));
    return result;
  }
};
