  mutations: {
    SAVE_TIPS: (state, tips) => {
      state.recent = tips;
    },
    CHANGE_RECENT: (state, remRecent) => {
      let item = state.recent.find(el => el.id === remRecent);
      state.removed.push(item);
      state.recent = state.recent.filter(el => el.id !== remRecent);
    },
    CHANGE_DONE: () => {},
    CHANGE_REMOVED: () => {}
  },
  actions: {
    GET_TIPS: ({ commit }) => {
      const token = localStorage.getItem("token");
      api.getTips(token).then(data => {
        commit("SAVE_TIPS", data);
      });
    }
  },
  getters: {}
