// import randomDate from "@/services/RandomDateGen";
import api from "@/api/alerts&tips";

export default {
  namespaced: true,
  state: {
    recent: [],
    done: [],
    removed: []
  },
  mutations: {
    CLEAR_ALERTS: state => {
      state.recent = [];
      state.done = [];
      state.removed = [];
    },
    SAVE_ALERTS: (state, alerts) => {
      state.recent = alerts;
    },
    CHANGE_RECENT: (state, remRecent) => {
      let item = state.recent.find(el => el.id === remRecent);
      state.removed.push(item);
      state.recent = state.recent.filter(el => el.id !== remRecent);
    },
    CHANGE_DONE: () => {},
    CHANGE_REMOVED: () => {}
  },
  actions: {
    GET_ALERTS: ({ commit }) => {
      const token = localStorage.getItem("token");
      api.getAlerts(token).then(data => {
        commit("SAVE_ALERTS", data);
      });
    }
  },
  getters: {}
};
