<template>
  <div class="modal-signup">
    <div class="header">
      <div class="arrow-back" @click="CLOSE_SIGNUP"></div>
      <div class="btn-close" @click="CLOSE_SELECT"></div>
    </div>
    <div class="title-block">
      <span>{{serviceName}}</span>
      <img
        :src="require('@/assets/images/search/' + serviceImage)"
        :alt="serviceName"
        draggable="false"
      />
    </div>
    <div id="analytics-signup-btn">
      <span>Sign Up</span>
    </div>
  </div>
</template>
<script>
import { mapMutations, mapState, mapActions } from "vuex";
export default {
  name: "GoogleSignup",
  computed: {
    ...mapState("auth", ["isGoogleAnalytics", "token"]),
    ...mapState("servicesSelect", ["serviceName", "serviceImage", "clientId", "scope"])
  },
  data() {
    return {
      gapi: window.gapi
    };
  },
  methods: {
    ...mapMutations([
      "CLOSE_SELECT",
      "ON_ISLOADING",
      "OFF_ISLOADING",
      "SET_ISWIDGETSETTINGS"
    ]),
    ...mapMutations("auth", ["GOOGLE_LOGIN_FAIL"]),
    ...mapMutations("servicesSelect", ["OPEN_SIGNUP", "CLOSE_SIGNUP"]),
    ...mapActions("auth", ["GOOGLE_LOGIN"]),
    ...mapActions("servicesSelect", ["GET_ANALYTICS_DATA", "GET_GSC_DATA"]),
    signup() {
        const options = {
          token: this.token,
          rout: () => {
            this.OFF_ISLOADING();
            this.SET_ISWIDGETSETTINGS(this.serviceName);
          },
          onError: () => {
            this.OFF_ISLOADING();
          }
        };
        switch(this.serviceName){
          case "Google Analytics":
            this.GET_ANALYTICS_DATA(options);
            break;
          case "Google Search Console":
            this.GET_GSC_DATA(options);
        }
    },
    close() {
      this.CLOSE_SELECT();
      this.CLOSE_SIGNUP();
      this.OFF_ISLOADING();
    },
    prompt({ code }) {
      let options = {
        code,
        rout: this.signup,
        service:`is${this.serviceName.split(' ').join('')}`,
        onError: () => {
          this.OFF_ISLOADING();
        }
      };
      this.GOOGLE_LOGIN(options);
    },
    onFail({ error }) {
      this.CLOSE_SIGNUP();
      this.OFF_ISLOADING();
      this.GOOGLE_LOGIN_FAIL({ Error: error });
    },
    onSuccess(gu) {
      this.CLOSE_SIGNUP();
      this.ON_ISLOADING();
      const options = {
        client_id: this.clientId,
        scope: this.scope,
        prompt: "consent"
      };
      gu.grantOfflineAccess(options).then(this.prompt, this.onFail);
    },
    init() {
      /*global gapi*/
      gapi.load("auth2", () => {
        const GoogleAuth = gapi.auth2.init({
          client_id: this.clientId
        });
        const btn = document.querySelector("#analytics-signup-btn");
        GoogleAuth.attachClickHandler(btn, {}, this.onSuccess, this.onFail);
      });
    }
  },
  mounted() {
    this.init();
  }
};
</script>
<style scoped>
.modal-signup {
  width: 30%;
  border-radius: 0.5vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 1.5vw;
  padding: 0.5vw;
  color: #ffffff;
  user-select: none;
  background-color: #262626;
  box-shadow: 0 0 5px 0.1px #f2f2f2;
}

.modal-signup img {
  height: 2vw;
  margin-left: 10px;
}

.header {
  width: 100%;
  display: flex;
  align-items: center;
}

img{
  font-size: small;
}

.arrow-back {
  width: 2vw;
  height: 1.5vw;
  background: url("../../assets/images/modal_widget_settings/arrow_back.svg")
    no-repeat center center / contain;
  cursor: pointer;
  transition: all 0.1s linear;
}

.arrow-back:hover {
  filter: drop-shadow(0px 0px 5px rgb(250, 250, 250));
}

.btn-close {
  width: 1.5vw;
  height: 1.5vw;
  background: url("../../assets/images/modal_widget_settings/btn_close.svg")
    no-repeat center center / contain;
  margin-left: auto;
  cursor: pointer;
  transition: all 0.1s linear;
}

.btn-close:hover {
  filter: drop-shadow(0px 0px 5px rgb(253, 2, 2));
}

#analytics-signup-btn {
  font-size: 1.5vw;
  padding: 0 1.5vw;
  margin: 1vw;
  cursor: pointer;
  background-color: #7873ff;
  border-radius: 0.5vw;
  transition: all 0.1s linear;
}

#analytics-signup-btn:hover {
  box-shadow: 0 0 7px #7873ff;
}

#analytics-signup-btn:active {
  box-shadow: inset 0 0 7px #000000;
  transform: scale(0.95);
}

.title-block {
  display: flex;
  font-size: 2vw;
}
</style>
