<template>
  <div id="google-search-btn" @click="click">
    <span>Google Search Console</span>
    <img
      src="@/assets/images/search/google_auth.png"
      alt="google-search"
      draggable="false"
    />
  </div>
</template>
<script>
import { mapMutations, mapState, mapActions } from "vuex";
export default {
  name: "GoogleSearchConsole",
  data() {
      return {
        name:"GoogleSearchConsole"
      };
    },
  computed: {
    ...mapState("auth", ["isGoogleSearchConsole", "token"])
  },
  methods: {
    ...mapMutations([
      "CLOSE_SELECT",
      "ON_ISLOADING",
      "OFF_ISLOADING",
      "SET_ISWIDGETSETTINGS"
    ]),
    ...mapMutations("servicesSelect", ["SET_RECENTS", "OPEN_SIGNUP", "SET_SERVICE"]),
    ...mapActions("servicesSelect", ["GET_GSC_DATA"]),
    async click() {
      this.SET_SERVICE(this.name);
      this.OFF_ISLOADING();
      this.SET_RECENTS(this.name);
      if (!this.isGoogleSearchConsole) {
        this.OPEN_SIGNUP(this.name);
        return;
      }
      this.ON_ISLOADING();
      const options = {
        token: this.token,
        rout: () => {
          this.OFF_ISLOADING();
          this.SET_ISWIDGETSETTINGS(this.name);
        },
        onError: () => {
          this.OFF_ISLOADING();
        }
      };
      this.GET_GSC_DATA(options);
      return;
    }
  }
};
</script>
<style scoped>
#google-search-btn {
  width: calc((100% / 3) - 30px); 
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.5vw;
  padding: 1.5vw 0;
  color: #ffffff;
  cursor: pointer;
  transition: all 0.1s linear;
  user-select: none;
  margin:10px 0 0 10px;
  background-color: #262626;
  box-shadow: 0 0 5px 0.1px #f2f2f2;
  opacity: 0.7;
}
#google-search-btn img {
  height: 2vw;
  margin-left: 10px;
}
#google-search-btn:hover {
  box-shadow: 0 0 7px 1px #f2f2f2;
  opacity: 1;
}
#google-search-btn:active {
  box-shadow: inset 0 0 5px 0.1px #f2f2f2;
  transform: scale(0.95);
  opacity: 1;
}
</style>
