import api from "@/api/servicesSelect";

export default {
  namespaced: true,
  state: {
    recents: JSON.parse(localStorage.getItem("recents")) || [],
    signup: false,
    scope: "https://www.googleapis.com/auth/userinfo.profile",
    serviceName: null,
    serviceImage: "@/assets/images/search/google_auth.png",
    clientId:
      "380470694344-0a4vb8rvio43bje2dmbs5hk7l8ecdglm.apps.googleusercontent.com",
    services: {
      GoogleAnalytics: {
        name: "Google Analytics",
        image: "google_analytix.png",
        scope:
          "https://www.googleapis.com/auth/userinfo.profile https://www.googleapis.com/auth/analytics.readonly"
      },
      GoogleSearchConsole: {
        name: "Google Search Console",
        image: "google_auth.png",
        scope:
          "https://www.googleapis.com/auth/userinfo.profile https://www.googleapis.com/auth/webmasters.readonly"
      }
    },
    AnalyticsProjects: [],
    GSCUrls: [],
    wData: null,
    metrics: [
      "ga_sessions",
      "ga_users",
      "ga_pageViews",
      "ga_pageViewsPerSession",
      "ga_avgSessionDuration",
      "ga_bounces",
      "ga_percentNewSessions"
    ]
  },

  mutations: {
    SET_RECENTS: (state, component) => {
      if (state.recents.includes(component)) {
        return;
      }
      state.recents.push(component);
      localStorage.setItem("recents", JSON.stringify(state.recents));
    },
    OPEN_SIGNUP: state => {
      state.signup = true;
    },
    CLOSE_SIGNUP: state => {
      state.signup = false;
    },
    SAVE_ANALYTICS: (state, data) => {
      state.AnalyticsProjects = data;
    },
    SAVE_DATA: (state, data) => {
      state.wData = data;
    },
    SAVE_GSC: (state, data) => {
      state.GSCUrls = data;
    },
    SET_SERVICE: (state, servName) => {
      state.serviceName = state.services[servName].name;
      state.serviceImage = state.services[servName].image;
      state.scope = state.services[servName].scope;
    }
  },

  actions: {
    SEND_ACC_RES: async ({ commit, dispatch }, gaData) => {
      gaData.token = localStorage.getItem("token");
      console.log(gaData);
      const dataFromSer = await api.sendAccAndRes(gaData);
      // const dataFromSer = {
      //   dates: [
      //     "2020-08-01",
      //     "2020-08-02",
      //     "2020-08-03",
      //     "2020-08-04",
      //     "2020-08-05",
      //     "2020-08-06",
      //     "2020-08-07"
      //   ],
      //   data: [1, 5, 9, 3, 12, 5, 7]
      // };

      const renderData = {
        metric: gaData.metric,
        dataFromSer
      };
      console.log(renderData);
      commit("SAVE_DATA", renderData);
      dispatch("GLOBAL_ADD_NEW_WIDGET", renderData, { root: true });
    },
    GET_GSC_DATA: async ({ commit }, { rout, token, onError }) => {
      const data = await api.fetchGSC(token);
      const { Error } = data;
      if (Error) {
        onError();
        console.log(Error);
        return;
      }
      console.log("Data of GSC projects ", JSON.stringify(data));
      commit("SAVE_GSC", data);
      rout();
    },
    GET_ANALYTICS_DATA: async ({ commit }, { rout, token, onError }) => {
      const data = await api.fetchAnalytics(token);
      const { Error } = data;
      if (Error) {
        onError();
        console.log(Error);
        return;
      }
      console.log("Data of Analytics projects ", JSON.stringify(data));
      commit("SAVE_ANALYTICS", data);
      rout();
    }
  }
};